import React from 'react';
import store from '../../redux/store';
import { Route } from 'react-router-dom';
import { SignupButton, SignupForm, SignupInput, SignupWrapper } from '../../styled';


const Signup = ({ path }) => {
    return (
        <Route exact path={path}>
            <SignupWrapper>
                <SignupForm>
                    <SignupInput />
                    <SignupInput />
                    <SignupButton>회원가입</SignupButton>
                </SignupForm>
            </SignupWrapper>
        </Route>
    )
};

export default Signup;