import { createStore } from "redux";

const actions = {
    login: "LOGIN",
    signup: "SIGNUP"
}

const initState = {

}

export default createStore((state, action) => {
    if (!state) {
        return initState;
    }

    else if (action.type === actions.login) {
        return { ...state, user: action.uuid }
    }

    else if (action.type === actions.signup) {
        return { ...state, user: action.uuid }
    }

    return state;
},
    window.__REDUX_DEVTOOLS_EXTENSION__ &&
    window.__REDUX_DEVTOOLS_EXTENSION__()
)