import React from 'react';
import { connect } from 'react-redux';
import { Switch } from 'react-router-dom';
import Template from './components/common/Template';

const meta = {
  title: "흔하디 흔한 TODO 앱",
  description: "React와 Google Sheets로 개발하여 약간은 특별할지도 모르는 TODO 앱",
  image: "",
  url: "https://choewy.github.io/react-todo-app"
};

const paths = {
  home: "/",
  login: "/login",
  signup: "/signup",
};

const App = () => {
  return (
    <Switch>
      <Template meta={meta} paths={paths} />
    </Switch>
  );
}

export default connect((state) => {
  return {
    ...state
  }
})(App);
